@import "@/scss/settings.scss";





































































.fade-enter-active {
  transition: opacity 800ms ease-in-out;
}
.fade-enter-to {
  opacity: 1;
}
.fade-enter {
  opacity: 0;
}

.box-image {
  display: flex;
  position: relative;
  overflow: hidden;
  position: relative;
}
.box-image img {
  position: absolute;
  top: 0;
  left: 0;
  bottom:0;
  right:0;
  height: 100%;
  width: 100%;
  transition: 800ms all ease-in-out;
} 
.blured {
  filter: blur(25px);
}

